mwl-calendar {
  .cal-row-head {
    [class*="cal-cell"] {
      border: none;
      padding: 5px 0;
      text-align: center;
      font-weight: bolder;
    }
    [class*="cal-cell"] small {
      font-weight: normal;
    }
  }
  .cal-year-box .row:hover,
  .cal-row-fluid:hover {
    background-color: @rowHover;
  }
  .cal-month-day {
    height: @rowHeightMonth;
  }

  [class*="cal-cell"]:hover,
  .cell-focus,
  [class*="cal-cell"] .drop-active,
  .cal-cell.drop-active,
  .cal-week-box .cal-cell1.drop-active,
  .cal-day-hour-part.drop-active {
    background-color: @dayHover;
  }

  .cal-year-box [class*="span"],
  .cal-month-box [class*="cal-cell"] {
    min-height: @rowHeightMonth;
    position: relative;
  }

  .cal-year-box,
  .cal-month-box {
    [class*="span"] + [class*="span"],
    [class*="cal-cell"] + [class*="cal-cell"] {
      border-left: @borderSizevert @borderStyle @borderColor;
    }
  }

  .cal-year-box [class*="span"] {
    min-height: @rowHeightYear;
  }

  .cal-year-box .row,
  .cal-month-box .cal-row-fluid {
    border-bottom: @borderSizehoriz @borderStyle @borderColor;
    margin-left: 0px;
    margin-right: 0px;
  }

  .cal-year-box,
  .cal-week-box {
    border-top: @borderSizehoriz @borderStyle @borderColor;
    border-bottom: @borderSizehoriz @borderStyle @borderColor;
    border-right: @borderSizevert @borderStyle @borderColor;
    border-left: @borderSizevert @borderStyle @borderColor;
    border-radius: 2px;
  }

  span[data-cal-date] {
    font-size: 1.2em;
    font-weight: normal;
    opacity: 0.5;
    transition: all .3s ease-in-out;
    -webkit-transition: all .1s ease-in-out;
    -moz-transition: all .1s ease-in-out;
    -ms-transition: all .1s ease-in-out;
    -o-transition: all .1s ease-in-out;
    margin-top: 15px;
    margin-right: 15px;
  }
  span[data-cal-date]:hover {
    opacity: 1;
  }

  .cal-day-outmonth span[data-cal-date] {
    opacity: 0.1;
    cursor: default;
  }

  .cal-day-today {
    background-color: #e8fde7;
  }

  .cal-day-today span[data-cal-date] {
    color: darkgreen;
  }
  .cal-month-box .cal-day-today span[data-cal-date] {
    font-size: 1.9em;
  }
  .cal-day-holiday span[data-cal-date] {
    color: #800080;
  }

  .cal-day-weekend span[data-cal-date] {
    color: darkred;
  }

  .cal-week-box-cell {
    border: @borderSize @borderStyle @borderColor;
    border-right: 0px;
    border-radius: 5px 0 0 5px;
    background-color: @rowHover;
    text-align: right;
  }

  .cal-day-tick {
    border: @borderSize @borderStyle @borderColor;
    border-top: 0px solid;
    border-radius: 0 0 5px 5px;
    background-color: @dayHover;
    text-align: center;

    .fa {
      display: none;
    }
  }

  .cal-day-tick {
    position: absolute;
    right: 50%;
    bottom: -21px;
    padding: 0px 5px;
    cursor: pointer;
    z-index: 5;
    text-align: center;
    width: 26px;
    margin-right: -17px;
  }
  .cal-slide-box {
    width: 16%;
  }
  .cal-slide-content {
    background-color: #fff;
    .data-cal-events-1 {
      height: 49px;
      width: 100%;
      padding: 4px 4px 0 4px;
      border-radius: 2px 0px 0px 2px;;
      background: rgba(36, 161, 72, 0.15);
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
      word-break: break-all;
      cursor: pointer;
      margin-top: 4px;
      &.published {
        color: #9BACBB;
        border-left: 2px rgba(36, 161, 72, 0.15) solid;
      }
      &.normal {
        color: #2F3E4B;
        border-left: 2px #4FB36C solid;
      }
      &:hover {
        color: #fff;
        background: #4FB36C;
      }
      .icon {
        width: 13px;
        height: 13px;
        background: #9BACBB;
      }
      .cal-events-title {
        font-size: 14px;
        font-weight: 600;
        width: 100%;
      }
      .cal-events-content {
        font-size: 12px;
        font-weight: 400;
        width: 100%;
      }
    }
    .data-cal-events-2 {
      height: 20px;
      width: 100%;
      padding: 1px 4px 0 4px;
      border-radius: 2px 0px 0px 2px;;
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
      word-break: break-all;
      cursor: pointer;
      margin-top: 4px;
      &.expired-unfinished {
        color: #FB6963;
        background: rgba(251, 105, 99, 0.15);
        border-left: 2px #FB6963 solid;
      }
      &.expired-finished {
        color: #9BACBB;
        background: rgba(54, 168, 199, 0.15);
        border-left: 2px rgba(54, 168, 199, 0.15) solid;
      }
      &.expiring {
        color: #2F3E4B;
        background: rgba(54, 168, 199, 0.15);
        border-left: 2px #5EB9D2 solid;
      }
      &.expired-unfinished:hover {
        color: #fff;
        background:#f1453f;
      }
      &.expired-finished:hover {
        color: #fff;
        background:#5EB9D2;
      }
      &.expiring:hover {
        color: #fff;
        background:#5EB9D2;
      }
      .icon {
        width: 13px;
        height: 13px;
      }
      .cal-events-title {
        font-size: 14px;
        width: 100%;
        font-weight: 600;
      }
    }
    .data-cal-events-3 {
      height: 20px;
      width: 100%;
      padding: 1px 4px 0 4px;
      border-radius: 2px 0px 0px 2px;;
      background: rgba(255, 154, 61, 0.15);
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
      word-break: break-all;
      cursor: pointer;
      margin-top: 4px;
      &.expired {
        color: #9BACBB;
        border-left: 2px rgba(255, 154, 61, 0.15) solid;
      }
      &.normal {
        color: #2F3E4B;
        border-left: 2px #FFAE63 solid;
      }
      &:hover {
        color: #fff;
        background: #FFAE63;
      }
      .icon {
        width: 13px;
        height: 13px;
      }
      .cal-events-title {
        width: 100%;
        font-size: 14px;
        font-weight: 600;
      }
    }
  }

  .cal-slide-content a.event-item {
    color: #ffffff;
    font-weight: normal;
  }

  a.event-item-edit,
  a.event-item-delete,
  a.event-item-action {
    padding-left: 5px;
  }

  .cal-year-box .cal-slide-content a.event-item,
  .cal-year-box a.event-item-edit,
  .cal-year-box a.event-item-delete,
  .cal-year-box a.event-item-action {
    position: relative;
    top: -3px;
  }

  .events-list {
    max-height: @rowHeightMonth  - 53px;
    padding-left: 5px;
  }
  .cal-column {
    border-left: @borderSize @borderStyle @borderColor;
  }
  a.cal-event-week {
    text-decoration: none;
    color: #151515;
  }
  .badge-important {
    background-color: #b94a48;
  }

  .pointer {
    cursor: pointer;
  }

  .cal-year-box:last-child {
    border-bottom: 0px;
  }

  .cal-year-div-qz {
    float: left;
    min-width: 160px;
  }
  .cal-year-qz-word {
    height: 20px;
    font-size: 14px;
    font-family: PingFangSC-Medium, PingFang SC;
    line-height: 20px;
  }
  .cal-year-qz-content {
    height: 49px;
    font-size: 14px;
    font-family: PingFangSC-Medium, PingFang SC;
    line-height: 49px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
  .qz-word-color1 {
    font-weight: 500;
    color: #9BACBB;
  }
  .qz-word-color2 {
    color: #2F3E4B;
    font-weight: 600;
  }
  .qz-word-color3 {
    color: #2F3E4B;
    font-weight: 400;
  }
  .qz-word-color4 {
    font-weight: 600;
    color: #FB6963;
  }
  
  .cal-context {
    width: 100%;
  }

  .cal-events-num {
    margin-top: 20px;
  }

  @media (max-width: 991px) {

    .cal-year-box [class*="span"]:nth-child(2) {
      border-right: 0px;
    }

    .cal-year-box [class*="span"]:nth-child(1), .cal-year-box [class*="span"]:nth-child(2) {
      border-bottom: 1px solid #e1e1e1;
    }

  }

}



